export default {
	data() {
		return {
			form:{
				usde: ''
			},
			userDetail:''
		};
	},
	onLoad:function(){
		this.fetchUserDetail()
	},
	methods:{
		allin:function(){
			this.form.usde = this.userDetail.usde
		},
		async fetchUserDetail(){
			const res  = await this.$http.get('/user/details')
			console.log(res)
			this.userDetail = res.data.data
			
		},
		async submit(){
			if (this.form.usde <0) { 
				uni.showToast({
					title: '數量不能少於零',
					icon: 'none'
				});
				return
			}
			uni.showLoading({
				title: '提交中',
				mask: true
			});
			const res = await this.$http.get('/deal/usde_signin',{data: this.form})
			uni.hideLoading()
			console.log(res)
			uni.showToast({
				title: '劃轉成功',
				icon: 'none',
				duration: 1000
			});
			setTimeout(function() {
				uni.navigateBack({
					delta: 1
				});
			}, 1500);
			
			 
		},
		scanCodeToken:function(){
			uni.scanCode({
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
			    }
			});
		},
		scanCodeNode: function(){
			uni.scanCode({
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
			    }
			});
		}
	}
}
